.cartContainer{
    display: flex;
    justify-content: space-between;
    background-color: #f1f3f6;
    height: 100vh;
    padding: 45px 0;
}
.deliverToContainer{
    display: flex;
    justify-content: space-between;
    background-color: #FFFFFF;
    padding: 17px 15px;
    align-items: center;

}
.ProductContainerCart{
    height: 55%;
    width: 60%;
    display: flex;
    flex-direction: column;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgb(209, 213, 219) 0px 0px 0px 1px inset;
    gap: 1.5rem;
}
.ProductContainerCartSection{
    background-color: #FFFFFF;
    padding: 50px 15px;
    display: flex;
    justify-content: space-between;
    height: auto;
}
.CartProduct{
    display: flex;
    justify-content: space-between;
    width: 55%;
    align-items: center;
}
.imgSectionCart{
    align-items: center;
    display: flex;
}
.CartImage{
    height: 140px
}
.decPara{
    display: flex;
    flex-direction: column;
    gap: 3px;
}
.decHolPara{
    display: flex;
    flex-direction: column;
    gap: 9px;
    font-size: 14px;
    letter-spacing: 0.5px;

}
.PriceShowProduct{
    display: flex;
    gap: 10px; 
    font-size: 14px;
    align-items: center;
    width: 103%;
}
.cartContentSection{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 84%;
}
.DescriptionPara{
    font-weight: 600;
    font-size: 15px;
}
.DesignProduct,.SellerName{
    font-weight: 100;
}
.PreviousPrice{
    color: grey;
    text-decoration: line-through;

}
.ActualPriceOfProduct{
    font-weight: 600;
    font-size: 17px;
}
.WhichPercentOffOfTheProduct,.WhatIsOfferApplyOfTheProduct{
    color: green;
    font-weight: 500;
}
.i_Icon{
    background-color: green;
    padding: 0 7px;
    border-radius: 50%;
    color: #FFFFFF;
    font-size: 13px; 
}
.DeliveryDateOfTheProduct{
    font-size: 14px;
}
.DeliveryFee{
    color: grey;
    text-decoration: line-through;
    
}
.FreeOrNot{
    font-weight: 500;
    color: green;
    font-size: 13px;
}
.DeliveryToPara{
    font-size: 14px;
}
.DistName{
    font-weight: 500;
    font-size: 15px;
}
.ChangeDeliveryPinCode{
    border: none;
    background-color: #FFFFFF;
    padding: 5px 10px;
    border-radius: 2px;
    font-weight: 500;
    cursor: pointer;
    border: 1px solid #bbbbbb;
    color: #02025b;
}
.ChangeDeliveryPinCode:hover{
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    border: 1px solid #FFFFFF;
}
.PriceContainerDetails{
    height: auto;
    background-color: #FFFFFF;
    width: 31%;
    height: 55%;
    box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
}
.priceDetailsSection{
    border-bottom: 1px solid #e3e3e3;
    padding: 12px 15px;
    color: lightslategray;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.3px;
    word-spacing: 2px;
}
.PriceData{
    list-style: none;
    padding: 12px 14px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 57%;
    border-bottom: 1px #e3e3e3 dashed;
}
.ItemsDataLis{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.DisPriceSpn{
    text-decoration: line-through;
    font-size: 14px;
    color: gray;
}
.FreeRup{
    font-size: 14px;
    color: green;
    font-weight: 500;
}
.DisPrice{
    display: flex;
    gap: 5px;
}
.totalAmount{
    display: flex;
    justify-content: space-between;
    padding: 12px 15px;
    align-items: center;
    border-bottom: 1px #e3e3e3 dashed;
    font-weight: bold;
}
.TotalSaveAmountOfTheProduct{
    background-color: #FFFFFF;
    padding: 12px 14px;
    color: green;
    font-weight: 600;
}
.ItemPricesDiscount{
    color: green;
    font-weight: 500;
}