.gripContainer{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    background-repeat: no-repeat;
    background-image: url(https://i.postimg.cc/QdDmxz3n/Vector-2.png);
    background-size: cover;
    height: 149vh;
}

.gripCircle{
    z-index: 2;
}
.GripParaPOP{
    width: 450px; 
    padding: 20px 0;
}
.gripContent{
    text-align: right;
}
.GripPhoneContent{
    font-weight: 600;
    font-size: 3rem;
    letter-spacing: 1px;
    color: #0c565b;
    margin-bottom: 15px !important;
}
.BestPriceGripContent{
    color: #0c565b;
    font-weight: 500;

}
.CroPrice{
    color: #24cfcf;
    font-weight: 500;
    text-decoration: line-through;
}
.ActualPrice{
    color: #f92a1e;
    font-size: 25px;
    font-weight: 700;

}
.PriceGripContent{
    display: flex;
    justify-content: right;
    align-items: center;
    gap: 5px;
    margin-bottom: 7px;
}
.GripBuyNowBtn{
    border: none;
    padding: 7px 14px;
    border-radius: 12px;
    font-weight: 500;
    color: white;
    background-color: #00d4e7;
    border: 2px solid #00d4e7;
    cursor: pointer;


}
.GripBuyNowBtn:hover{
    background-color: white;
    color: #00d4e7;
    border: 2px solid #00d4e7;
    cursor: pointer;
}