.CardImage{
    height: 200px;
}
.card{
    width: 470px !important;
    height: 78vh !important;
    display: flex !important;
    align-items: center !important;
    justify-content: space-evenly !important;
    border-radius: 44px !important;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px !important;
    position: relative;
    background-color: #FFFFFF;
}

.MainContainer{
    display: flex;
align-items: center;
justify-content: space-evenly;

background-image: url('https://i.postimg.cc/dQBcKD6Y/Vector-1.png');
background-size: cover;
}
.MainContentContainer{
    width: 54%;
    height: 68vh;
}
.MainParaGraph{
    padding-right: 239px;
    margin: 26px 0;
    letter-spacing: 0.5px;
    word-spacing: 1px;
    color: #FFFFFF;
}
.MainCrystalShop{
    font-size: 4rem !important;
    color: #FFFFFF;
    letter-spacing: 1px;

}
.MainWelcomeHeading{
    color: #FFFFFF;
    font-size: 1.5rem;
    letter-spacing: 1px;
    font-weight: 500;

}
.MainBothButton{
    display: flex;
    gap: 3rem;
}
.MainBtn{
    border: none;
    padding: 9px 12px;
    border-radius: 10px;
    font-size: 15px;
    color: #078181;
    background-color: #FFFFFF;
    cursor: pointer;
}
.MainBtn:hover{
    border: none;
    padding: 9px 12px;
    border-radius: 10px;
    font-size: 15px;
    background-color: #078181;
    color: #FFFFFF;
}
.CardImgTitlePrice{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
}
.Card_newLot{
    position: absolute;
    left: 0;
    top: 7rem;
    background-color: #705ce1;
    color: #FFFFFF;
    padding: 4px 12px;
    border-radius: 5px;
    font-weight: 500;

}
.CardCrystalPara{
    color: gray;
    font-size: 16px;
    font-weight: 500;

}
.dollarPrice{
    color: #004cc3;
    font-weight: bold;
}