.Navbar{
    padding: 1rem 0;
}
.navbarContainer{
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: 33rem;
}
.LogoImg{
    height: 4rem;
}
.LinksUl{
    display: flex;
    list-style: none;
    gap: 2rem;
}
.cartIconContainer{
    display: flex;
    align-items: center;
    gap: 5px;
}
.Counter{
    background-color: rgb(245, 64, 41);
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    padding: 0 2px;
}

.Lists{
    color: #FFFFFF;
    font-size: 18px;
    cursor: pointer;
}
.Lists a{
    color: #FFFFFF;
    font-size: 18px;
    cursor: pointer;
    text-decoration: none;
}
.Lists a:hover{
    color: #0c537f;
}
.NavbarSpan{
    font-size: 18px;
}
.linksLists{
    height: 5vh;
}