.AllProductContainer{
    display: flex;
    flex-direction: column;
    gap: 5rem;
    background-color: #FFFFFF;
    padding-top: 80px;

}
.HeadingAllProduct{
    text-align: center !important;
    color: #0c565b;
    font-weight: bold;
}
.ProductCategory{
    display: flex;
    gap: 7rem;
    padding: 0 57px;
    align-items: center;
}
.FilterCategory, .SoftCategory{
    display: flex;
    gap: 3px;
}
.ProductSelect{
    outline: none;
    border: none;
    font-weight: 700;
    color: #0c537f;
}
.label{
    color: #8d8d8d;
    font-weight: 600;
}
.productCard{
    width: 373px !important;
    height: 72vh !important;
    display: flex !important;
    align-items: center !important;
    justify-content: space-evenly !important;
    border-radius: 44px !important;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px !important;
}
.ProductListsContainer{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
    gap: 2rem;
    padding: 0 50px;
}
.ViewAllBtn{
    border: none;
    padding: 6px 27px;
    color: #4fedfb;
    background-color: #FFFFFF;
    border-radius: 12px;
    cursor: pointer;
    border: 2px solid #4fedfb;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    -webkit-border-radius: 12px;
    -moz-border-radius: 12px;
    -ms-border-radius: 12px;
    -o-border-radius: 12px;
}
.ViewAllBtn:hover{
    background-color: #4fedfb;
    color: #FFFFFF;
    border: 2px solid #FFFFFF;
}
.btnProduct{
    text-align: center;
}